<template>
  <div class="login-container">
    <h1>登录</h1>
    <form @submit.prevent="submitForm">
      <div class="form-group">
        <label for="username">用户名:</label>
        <input type="text" id="username" v-model="form.username" required />
      </div>
      <div class="form-group">
        <label for="password">密码:</label>
        <input type="password" id="password" v-model="form.password" required />
      </div>
      <div class="form-group">
        <label for="age">年龄:</label>
        <input type="number" id="age" v-model.number="form.age" />
      </div>
      <div class="form-group">
        <label for="phone">手机号:</label>
        <input type="text" id="phone" v-model="form.phone" />
      </div>
      <div class="form-group">
        <label for="idCard">身份证号:</label>
        <input type="text" id="idCard" v-model="form.IDCard" />
      </div>
      <div class="form-group">
        <button type="submit">登录</button>
      </div>
    </form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "LoginPage",
  data() {
    return {
      form: {
        username: "",
        password: "",
        age: "",
        phone: "",
        IDCard: "",
      },
      errors: {
        username: null,
        password: null,
        age: null,
        phone: null,
        IDCard: null,
      },
    };
  },
  methods: {
    validateForm() {
      this.errors = {
        // 重置错误
        username: this.form.username ? null : "用户名是必填项",
        password: this.form.password ? null : "密码是必填项",
        age: this.form.age && !isNaN(this.form.age) ? null : "年龄必须为数字",
        phone: this.form.phone.match(/1[3456789]\d{9}/)
          ? null
          : "手机号格式不正确",
        IDCard: this.form.IDCard.match(/\d{17}[\dxX]/)
          ? null
          : "身份证号码格式不正确",
      };
      // 确认是否存在错误
      return Object.values(this.errors).every((x) => x === null);
    },
    submitForm() {
      if (this.validateForm()) {
        // 执行表单校验
        axios
          .post("http://localhost:8080/user/add", this.form)
          .then((response) => {
            if (response.status === 200) {
              alert("注册成功");
            }
          })
          .catch((error) => {
            alert("出错了: " + error);
          });
      } else {
        // 如果验证未通过，则弹出错误信息
        const errorMessages = Object.values(this.errors).filter(
          (error) => error
        );
        alert("校验错误:\n" + errorMessages.join("\n"));
      }
    },
  },
};
</script>

